.media {
  padding: 1rem 0 2.5rem;
}
@media screen and (width <= 768px) {
  .media {
    padding-bottom: 0;
  }
}
.media__breadcrumbs {
  margin-bottom: 5rem;
}
@media screen and (width <= 768px) {
  .media__breadcrumbs {
    margin-bottom: 2.5rem;
  }
}
.media__title {
  font-size: 3.5rem;
  margin-bottom: 2rem;
}
@media screen and (width <= 768px) {
  .media__title {
    font-size: 2rem;
  }
}
.media__subtitle {
  margin-bottom: 4rem;
}
@media screen and (width <= 768px) {
  .media__subtitle {
    margin-bottom: 1.5rem;
  }
}
.media__filter {
  padding-top: 2.5rem;
  margin-bottom: 2.5rem;
  border-top: 1px solid rgba(180, 180, 180, 0.2);
}
@media screen and (width <= 768px) {
  .media__filter {
    border-top: transparent;
    margin-bottom: 1.5rem;
    padding-top: 0;
    row-gap: 1.5rem;
  }
}
.media__tabs-head {
  margin-bottom: 1.5rem;
}
.media__list {
  margin-bottom: 2.5rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;
}
@media screen and (width <= 1230px) {
  .media__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (width <= 768px) {
  .media__list {
    margin-bottom: 1.5rem;
  }
}
@media screen and (width <= 576px) {
  .media__list {
    grid-template-columns: repeat(1, 1fr);
  }
}
.media__list-video {
  margin-bottom: 2.5rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem 0.5rem;
}
@media screen and (width <= 1230px) {
  .media__list-video {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (width <= 768px) {
  .media__list-video {
    margin-bottom: 1.5rem;
  }
}
@media screen and (width <= 576px) {
  .media__list-video {
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
}
.media__more-btn {
  width: 100%;
  margin-bottom: 1rem;
}
@media screen and (width <= 768px) {
  .media__more-btn {
    margin-bottom: 0.25rem;
    font-size: 1.125rem;
  }
}
.media__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid rgba(180, 180, 180, 0.2);
  border-bottom: 1px solid rgba(180, 180, 180, 0.2);
  font-family: var(--font-pt);
  padding: 2rem 0;
  margin-bottom: 2.5rem;
}
@media (max-width: 480px) {
  .media__info {
    gap: 0.5rem;
  }
}
.media__info-left {
  display: flex;
  align-items: center;
  gap: 2.5rem;
}
@media screen and (width <= 768px) {
  .media__info-left {
    flex-direction: row-reverse;
    justify-content: space-between;
    width: 100%;
  }
}
.media__info-date, .media__info-city, .media__info-share {
  display: flex;
  align-items: center;
  font-weight: 700;
  gap: 0.25rem;
}
.media__info-share {
  transition: color 0.3s;
}
@media screen and (width <= 768px) {
  .media__info-share {
    display: none;
  }
}
.media__info-share:hover {
  color: var(--color-red);
}
.media__info-share-mobile {
  transition: color 0.3s;
  display: none;
  align-items: center;
  font-weight: 700;
  gap: 0.25rem;
  border-top: 1px solid rgba(180, 180, 180, 0.2);
  border-bottom: 1px solid rgba(180, 180, 180, 0.2);
  padding: 1rem 0;
  margin-top: 1rem;
  font-family: var(--font-pt);
}
@media screen and (width <= 768px) {
  .media__info-share-mobile {
    display: flex;
  }
}
.media__info-share-mobile:hover {
  color: var(--color-red);
}
.media .news__card {
  background-color: var(--light-color);
  padding: 1rem;
  border-radius: 1rem;
}
.media__gallery-grid {
  margin-bottom: 1.5rem;
}
.media__popular-title {
  margin-bottom: 2.5rem;
  margin-top: 6.5rem;
}
@media screen and (width <= 768px) {
  .media__popular-title {
    margin-bottom: 1.5rem;
    margin-top: 4rem;
  }
}
.media__video {
  margin-bottom: 2rem;
}
@media screen and (width <= 768px) {
  .media__video {
    margin-bottom: 1rem;
  }
}
.media__text {
  font-family: var(--font-pt);
}